















































@use 'src/styles/responsive' as r;
@use 'src/styles/mixins';
@use 'src/styles/typography';
@use "src/styles/color";

.post {
    display: flex;
    padding: 1rem;
    flex-direction: column;
    overflow-x: hidden;

    @include mixins.interactive;

    .region,
    .title {
        margin-bottom: 1rem;
    }

    article .title {
        @include mixins.type-h4;
    }

    &[scale='0'] {
        .title {
            @include mixins.type-h1;
            margin-bottom: 2rem;
        }

        .gallery {
            margin-bottom: 2rem;
        }

        .description {
            margin-bottom: 2rem;
        }

        @include r.lt-md {
            article {
                .title {
                    @include mixins.type-h4;
                }

                .gallery {
                    display: grid;
                    grid-gap: 1rem;
                    grid-auto-columns: 8rem;
                    grid-auto-flow: column;
                    overflow-x: auto;
                    padding-left: 1rem;
                    margin: 0 -1rem 2rem -1rem;

                    // Fix scroll padding bug
                    &::after {
                        content: '';
                        grid-row: 1;
                        width: 1px;
                    }
                }
            }
        }

        $padding: 2rem;

        @include r.md {
            padding: $padding;

            article .gallery {
                display: flex;
                grid-gap: 1rem;
                flex-wrap: wrap;
            }
        }

        @include r.lg {
            position: relative;

            .region {
                position: absolute;
                top: $padding;
                left: $padding;
            }

            article {
                display: flex;
                flex-direction: column;

                .title {
                    width: typography.$max-line-width;
                }

                .gallery {
                    min-width: typography.$max-line-width;
                    margin-left: 5rem;
                    margin-right: auto;

                    @media (min-width: 1120px) {
                        margin-left: auto;
                    }
                }

                .title,
                .markdown h1,
                .markdown h2,
                .markdown h3,
                .markdown h4,
                .markdown h5,
                .markdown h6,
                .markdown p,
                .markdown td,
                .markdown ol,
                .markdown ul,
                .markdown pre {
                    max-width: typography.$max-line-width;
                    margin-left: 5rem;
                    margin-right: auto;

                    @media (min-width: 1120px) {
                        margin-left: auto;
                    }
                }
            }
        }
    }

    // clamp title to two line on linear view
    &[scale='1'],
    &[scale='2'] {
        .gallery {
            display: grid;
            grid-gap: 1rem;
            grid-template-columns: repeat(auto-fit, 8rem);
            overflow-x: auto;
            position: relative;
            padding: 4px 4px 4px 1rem;
            margin: -4px -1rem calc(1rem - 4px) -1rem;

            // Fix scroll padding bug
            &::after {
                content: '';
                grid-row: 1;
                width: 1px;
            }

            // Hide scrollbars on desktop (still scrollable in mobile)
            &::-webkit-scrollbar {
                height: 0;
            }

            .picture {
                grid-row: 1;
            }
        }
    }
}
