












































































































@use 'src/styles/responsive' as r;
@use 'src/styles/color';
@use 'src/styles/shape';

$margin: 2.5rem;

.drawer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    width: 16rem;
    max-width: 100%;
    height: 100%;
    z-index: 100;
    left: 0;
    transition: width 300ms ease-in-out;
    padding: 1rem 1rem $margin 0;
    border-right: shape.$border;

    &[collapse] {
        left: -16rem;
    }

    .actions {
        display: flex;
        align-items: center;
        justify-content: space-between;
        align-self: stretch;
        margin: 0 0 calc(1rem + 2px) $margin;
        height: 2.25rem;

        .logo:hover {
            background-color: transparent; // hide hover effect
        }
    }

    .tabs {
        align-self: stretch;
        margin-bottom: auto;

        li {
            width: 100%;

            &:not(:last-child) {
                margin-bottom: 0.25rem;
            }

            a {
                display: flex;
                border-radius: 0 shape.$radius shape.$radius 0;
                padding-left: $margin;
                height: $margin;
                align-items: center;

                &:hover,
                &:focus {
                    background-color: rgba(var(--primary), color.$alpha-hover);
                }

                &[selected] {
                    background-color: color.$primary;
                    color: color.$on-primary;
                }
            }
        }
    }

    .options {
        margin: 0 0 $margin $margin;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        > *:not(:last-child) {
            margin-bottom: 1rem;
        }
    }

    .socials {
        margin-left: $margin;
    }

    @include r.md {
        position: static;
        overflow-x: hidden;
        padding-right: 0;
        border: none;

        &[collapse] {
            left: 0;
            width: 0;
        }
    }

    @include r.lg {
        &[collapse] {
            left: 0;
            width: 16rem;
        }

        .actions .close {
            display: none;
        }
    }
}
