




































































@use 'src/styles/shape';
@use 'src/styles/color';

#gallery {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    display: flex;
    flex-direction: column;

    .actions {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        margin: 1rem;
        z-index: 101;
        display: flex;
        justify-content: space-between;
    }

    .pictures {
        flex: 1;
        display: flex;
        overflow-x: auto;
        scroll-snap-type: x mandatory;

        .picture {
            scroll-snap-align: center;
            flex: auto 1 0;
            width: 100%;
            height: 100%;

            img {
                object-fit: contain;
            }
        }
    }

    .thumbnails {
        display: flex;
        overflow-x: auto;
        padding: 1rem;

        // Fix bottom padding bug
        &::after {
            display: block;
            content: '';
            min-width: 1rem;
        }

        .picture {
            flex: auto 0 0;
            height: 8rem;
            width: 8rem;
            border-radius: shape.$radius;
            transition: box-shadow 100ms ease-in-out;
            cursor: pointer;

            &:not(:last-child) {
                margin-right: 1rem;
            }

            &:hover,
            &:focus {
                box-shadow: 0 0 0 2px color.$primary;
            }
        }
    }
}
