



































@use 'src/styles/color';
@use 'src/styles/shape';

.gallery {
    .picture {
        height: 8rem;
        width: 8rem;
        border-radius: shape.$radius;
        transition: box-shadow 100ms ease-in-out;
        cursor: pointer;

        &:hover,
        &:focus {
            box-shadow: 0 0 0 2px color.$primary;
        }
    }
}
