


















@use "src/styles/color";
@use "src/styles/typography";

input {
    width: 100%;
    display: block;
    color: inherit;
    border: none;
    background-color: transparent;
    height: 2.25rem;
    outline: none;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    opacity: 0.54;

    &:focus {
        border-color: color.$primary;
        opacity: 1;

        &::placeholder {
            opacity: 0.54;
        }
    }

    &::placeholder {
        color: inherit;
    }
}
